import React, { Component } from 'react';
import Navbar from '../components/DatLeNhaDan/Navbar';
import MainBanner from '../components/DatLeNhaDan/MainBanner';
import Introduction1 from '../components/DatLeNhaDan/Introduction1';
import Introduction2 from '../components/DatLeNhaDan/Introduction2';
import Contact from '../components/Common/Contact';
import Footer from '../components/Layouts/Footer';

class Index2 extends Component {
    render() {
        return (
            <React.Fragment>
                <Navbar /> 
                <MainBanner />
                <Introduction1 />
                <Introduction2 />
                <Contact />
                <Footer />
            </React.Fragment>
        );
    }
}

export default Index2;